const initialState = {
  data: [],
};

export const Manageturntypeinitialdata = (state = initialState, action) => {
  let newState = { ...state };
  switch (action.type) {
    case 'MANAGE_TURNTYPE_JSON_RESPONSE':
      newState.data = [...action.json.data];
      return newState;
    default: {
      return state;
    }
  }
};
