import alertConstant from './alert.constant';

const initialState = {
  isOpen: false,
  content: { title: 'Alert', content: '' },
};

export function alertReducer(state = initialState, action) {
  let newState = { ...state };
  if (action.type === alertConstant.ALERT_OPEN) {
    newState = { isOpen: true, content: { ...action.payload } };
    return newState;
  }
  if (action.type === alertConstant.ALERT_CANCEL) {
    newState.isOpen = false;
    return newState;
  }
  return state;
}

