import { combineReducers } from 'redux';
import { Stationinitialdata } from './Station/reducer';
import { routerReducer } from 'react-router-redux';
import { userReducer } from './login/user.reducer';
import { Managedepartmentinitialdata } from './ManageDepartment/reducer';
import { Manageturntypeinitialdata } from './ManageTurnType/reducer';
import { Managetaskinitialdata } from './ManageTask/reducer';
import { TurnTemplateInitialData } from './TemplateList/reducer';
import { fltMntTemplateReducer } from './flightManagement/template/template.reducer';
import { fltUpdateReducer } from './flightManagement/flightUpdate/flightUpdate.reducer';
import commonActivityReducer from './commonActivity';
import fltMntCrewOnFlightReducer from './flightManagement/crewOnFlight/crewOnFlight.reducer';
import { Aircraftinitialdata } from './AircraftReg/reducer';
import { AircrafttypeReducer } from './AircraftType/reducer';
import { DelayCodesReducer } from './DelayCodes/reducer';
import fileuploadReducer from './flightManagement/fileUpload/file-upload.reducer';
import { fltTurnAroundReducer } from './flightManagement/flightUpdate/flightTurnAround/flightTurnAround.reducer';
const rootReducer = combineReducers({
  authentication: userReducer,
  routing: routerReducer,
  Managedepartmentinitialdatareducer: Managedepartmentinitialdata,
  Manageturntypeinitialdatareducer: Manageturntypeinitialdata,
  Managetaskinitialdatareducer: Managetaskinitialdata,
  TurnTemplateInitialDatareducer: TurnTemplateInitialData,
  flightManagementTemplate: fltMntTemplateReducer,
  fltMngtUpdate: fltUpdateReducer,
  commonActivityReducer,
  fltMntCrewOnFlightReducer: fltMntCrewOnFlightReducer,
  Stationinitialdata: Stationinitialdata,
  Aircraftinitialdata: Aircraftinitialdata,
  Aircrafttypeinitialdata: AircrafttypeReducer,
  DelayCodesReducer: DelayCodesReducer,
  fileuploadReducer: fileuploadReducer,
  fltTurnAroundReducer: fltTurnAroundReducer,
});
export default rootReducer;
