import axios from 'axios';
import environment from '..';
import { call } from 'redux-saga/effects';

class Service {
  service;
  constructor() {
    const service = axios.create({
      headers: {
        'Content-Type': 'application/json',
      },
    });
    service.interceptors.response.use(this.handleSuccess, this.handleError);

    this.service = service;
  }

  handleSuccess(response) {
    const commonAction = require('../Store/commonActivity/action').commonAction;
    const history = require('../Store/store').history;
    console.log(response.data.status, commonAction, history, 'testttt');

    // switch (response.data.status) {
    //   case 401:
    //     commonAction.openAlert({
    //       title: 'Warning',
    //       content: 'Token Expired Please Login Again... ',
    //     });
    //     history.push('/login');
    //     break;
    //   // case 404:
    //   //   this.redirectTo(document, '/404');
    //   //   break;
    //   // default:
    //   //   this.redirectTo(document, '/500');
    //   //   break;
    // }
    return response;
  }

  handleError = error => {
    const commonAction = require('../Store/commonActivity/action');
    const history = require('../Store/store');
    console.log(error.response.status, 'testtttttt');

    switch (error.response.status) {
      case 401:
        commonAction.openAlert({
          title: 'Warning',
          text: 'Token Expired Please Login Again... ',
        });
        history.push('/login');
        break;
      // case 404:
      //   this.redirectTo(document, '/404');
      //   break;
      // default:
      //   this.redirectTo(document, '/500');
      //   break;
    }
    return Promise.reject(error);
    console.log(error);
  };

  // redirectTo = (document, path) => {
  //   document.location = path;
  // };

  get = path => {
    return this.service.get(path).then(response => response);
  };

  patch(path, payload, callback) {
    return this.service
      .request({
        method: 'PATCH',
        url: path,
        responseType: 'json',
        data: payload,
      })
      .then(response => callback(response.status, response.data));
  }

  post = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
    }
  ) => {
    return this.service
      .request({
        method: 'POST',
        url: path,
        responseType: 'json',
        data: payload,
        headers: header,
      })
      .then(response => {
        return response.data;
      });
  };

  put = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
    }
  ) => {
    return this.service
      .request({
        method: 'PUT',
        url: path,
        responseType: 'json',
        data: payload,
        headers: header,
      })
      .then(response => response.data);
  };

  delete = (
    path,
    payload,
    header = {
      'Content-Type': 'application/json',
    }
  ) => {
    return this.service
      .request({
        method: 'DELETE',
        url: path,
        responseType: 'json',
        data: payload,
        headers: header,
      })
      .then(response => response.data);
  };
  // for saga purpose
  sagaGet = path => {
    console.log(path, 'path');
    return this.service.get(path).then(res => res);
  };
}

const service = new Service();
export default service;

// let tokenLocal = localStorage.getItem('token');
export function* APIcall(
  method,
  uri,
  payload,
  headers = {
    'Content-Type': 'application/json',
    token: localStorage.getItem('token'),
  }
) {
  console.log(headers, 'headers');
  if (environment.isDevelopment) {
    let options = {
      method: method,
      uri: uri,
      headers: headers,
    };
    if (payload) {
      options.body = payload;
    }
    console.log('options', options);
    return yield call(service.post, environment.Proxyserver, options);
  } else {
    const callType = method.toLowerCase();
    return yield call(service[callType], uri, payload, headers);
  }
}
