import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';
import { commonAction } from '../commonActivity/action';

const { setLoader, removeLoader } = loaderAction;

export const ManageDepartmentActions = {
  getDepartmentDetails: () => {
    return { type: 'GET_DEPARTMENT_DETAILS_ASYNC' };
  },
  postDepDetails: obj => {
    return { type: 'POST_DEPT_DETAILS', payload: obj };
  },
  deleteDepartmentDetails: val => {
    return { type: 'DELETE_DEPT_DETAILS', payload: val };
  },
};

const methodPost = 'POST'
const methodGET = 'GET'
const methodDelete = 'DELETE'
// const methodPut = 'PUT'
function* DeleteDepartment({ payload }) {
  try {
    yield put(setLoader());
    var val = JSON.stringify(payload);
    console.log(
      environment.TurnManagement.manageDepartment + '/' + payload.id,
      'department delete'
    );
    const url = environment.TurnManagement.manageDepartment + '/' + payload.id;
    //+
    // '?' +
    // localStorage.getItem('token');
    const value = yield APIcall(methodDelete, url);
    yield put(ManageDepartmentActions.getDepartmentDetails());
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'Department',
      content: payload.id + ' ' + 'is Deleted from List ',
    });
  } catch (e) {
    console.log(e);
  }
}

function* Managedepartmentdata() {
  try {
    yield put(setLoader());
    const url = environment.TurnManagement.manageDepartment;
    //  +
    // '?' +
    // localStorage.getItem('token');
    const val = yield APIcall(methodGET, url);
    console.log(val, 'managedepartm');
    if (val.status >= 200 && val.status <= 299) {
      yield put({ type: 'MANAGE_DEPARTMENT_JSON_RESPONSE', json: val });
      yield put(removeLoader());
    }
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
  }
}

function* AddDepartment({ payload }) {
  try {
    var val = JSON.stringify(payload);
    console.log(val, 'departmentcall');
    // var options = {
    //   url:
    //     environment.TurnManagement.createDepartment +
    //     '?' +
    //     localStorage.getItem('token'),
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json',
    //   },
    //   body: payload,
    // };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );
    
    // const methodName = 'POST'
    const value = yield APIcall(
      methodPost,
      environment.TurnManagement.createDepartment,
      val
    );
    //if(value.status >=200 && value.status <= 299){
    console.log(value, 'from add department');
    yield put(ManageDepartmentActions.getDepartmentDetails());
    commonAction.openAlert({
      title: 'Department',
      content: 'New Department ' + 'is Added To The' + ' ' + 'List',
    });
    // }
  } catch (e) {
    console.log(e);
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_DEPARTMENT_DETAILS_ASYNC', Managedepartmentdata);
  yield takeLatest('POST_DEPT_DETAILS', AddDepartment);
  yield takeLatest('DELETE_DEPT_DETAILS', DeleteDepartment);
}
