const initialState = {
  data: [],
};
export const Managetaskinitialdata = (state = initialState, action) => {
  let newState = { ...state };
  switch (action.type) {
    case 'MANAGE_TASK_JSON_RESPONSE':
      newState.data = [...action.json];
      return newState;
    default: {
      return state;
    }
  }
};
