const delatCodesInitialState = [
  {
    id: '',
    category: '',
    delaycategorygroup: '',
    description: '',
    subcategory: '',
    subcatcode: '',
    controllableind: '',
  },
];

export const DelayCodesReducer = (state = delatCodesInitialState, action) => {
  let newState = [...state];
  switch (action.type) {
    case 'Aircraft_DelayCodes_JsonResponse':
      newState = [...action.payload];

      return newState;

    default: {
      return newState;
    }
  }
};
