import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';

const { setLoader, removeLoader } = loaderAction;

export const StationActions = {
  getStationDetails: () => {
    return { type: 'GET_Station_Details_Async' };
  },
  postStationDetails: (obj, isEdit) => {
    return { type: 'POST_Station_Details', payload: { obj, isEdit } };
  },
  deleteStationDetails: id => {
    return { type: 'DELETE_Station_Details', payload: id };
  },
};



const methodPost = 'POST'
const methodGET = 'GET'
const methodDelete = 'DELETE'
const methodPut = 'PUT'

function* Stationdata() {
  try {
    yield put(setLoader());
    const url =
      environment.Datamaintenance.stationList
    //  +
    // '?' +
    // localStorage.getItem('token');

    const val = yield APIcall(methodGET, url);
    console.log(val, 'test');
    // yield put({ type: 'Station_JsonResponse', payload: val.data.content });
    yield put({ type: 'Station_JsonResponse', payload: val.data });
    console.log(val, 'test');

    yield put(removeLoader());
  } catch (e) {
    yield put(removeLoader());
  }
}

function* AddStation({ payload }) {
  try {
    let value;
    if (payload.isEdit) {
      var options = {
        url:
          environment.Datamaintenance.stationList +
          '/' +
          payload.obj.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPut,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      // console.log(value);

      value = yield APIcall(
        methodPut,
        environment.Datamaintenance.stationList +
        '/' +
        payload.obj.station,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        payload.obj
      );
    } else {
      var options = {
        url:
          environment.Datamaintenance.stationList,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPost,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield APIcall(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      value = yield APIcall(
        methodPost,
        environment.Datamaintenance.stationList,
        //  +
        //   '?' +
        //   localStorage.getItem('token'),
        payload.obj
      );
    }
    console.log(value, 'dharshan');

    yield put(StationActions.getStationDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}

function* DeleteStation({ payload }) {
  try {
    var options = {
      url:
        environment.Datamaintenance.stationList +
        '/' +
        payload,
      //  +
      // '?' +
      // localStorage.getItem('token'),
      method: methodDelete,
      headers: {
        'Content-Type': 'application/json',
      },
      body: payload,
    };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );
    // console.log(value);
    const value = yield APIcall(
      methodDelete,
      environment.Datamaintenance.stationList +
      '/' +
      payload
      //  +
      // '?' +
      // localStorage.getItem('token')
    );
    yield put(StationActions.getStationDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_Station_Details_Async', Stationdata);
  yield takeLatest('POST_Station_Details', AddStation);
  yield takeLatest('DELETE_Station_Details', DeleteStation);
}
