import {
  isTokenExpired,
  getAuth,
  setAuthorization,
  setAccessToken,
} from '../auth/auth';
import service, { APIcall } from './service';
import environment from '..';
import JwtDecode from 'jwt-decode';
let load = true;
let count = 0;
function getToken(refresh_token, next, action) {
  //alert('inside Token');

  const headers = {
    'Content-Type': 'application/x-www-form-urlencoded',
    Authorization:
      'Basic c3ByaW5nLXNlY3VyaXR5LW9hdXRoMi1yZWFkLWNsaWVudDpzcHJpbmctc2VjdXJpdHktb2F1dGgyLXJlYWQtY2xpZW50LXBhc3N3b3JkMTIzNA==',
    scope: 'read',
  };
  const query = {
    grant_type: 'refresh_token',
    refresh_token,
    client_id: 'spring-security-oauth2-read-client',
    client_secret: 'spring-security-oauth2-read-client-password1234',
  };

  var queryString = Object.keys(query)
    .map(key => key + '=' + query[key])
    .join('&');
  let options = {};
  
  if (environment.isDevelopment) {
    const methodName = 'POST'
    options = {
      method: methodName,
      uri: environment.login,
      headers: headers,
    };
    options.body = queryString;
  }
  console.log(options, 'refreshoption');
  return service
    .post(environment.Proxyserver, options)
    .then(response => {
      const decodedValue = JwtDecode(response.access_token);
      let credential = {
        userId: decodedValue.userId,
        access_token: response.access_token,
        user_name: decodedValue.user_name,
        expires_in: response.expires_in,
        // new Date(new Date().getTime() + response.expires_in * 1000)
        authdata: headers.Authorization,
        loginTime: new Date(),
        refresh_token: response.refresh_token,
      };
      console.log(credential, 'inside credential');

      setAuthorization(credential);
      setAccessToken(credential.access_token);
      next(action);
    })
    .catch(e => {
      console.log(e, 'Inside Catch Block credential');
      // next(action);
    });
  //console.log(response);
}

export default function requestMiddleware() {
  return ({ dispatch }) => next => action => {
    const { request } = action;
    if (request === undefined) {
      return next(action);
    }

    setInterval(() => {
      const { refresh_token } = getAuth();
      if (refresh_token && isTokenExpired() && count === 0) {
        count = 1;
        setTimeout(() => {
          count = 0;
        }, 2000);
        return getToken(refresh_token, next, action);
      }
    }, 30000);
    // return ({ dispatch }) => next => action => {
    //   const { refresh_token } = getAuth();
    //   if (refresh_token && isTokenExpired()) {
    //     getToken(refresh_token, next, action);
    //     return next(action);
    //   } else {
    //     return next(action);
    //   }
    // };

    return next(action);
  };
}

