import { put, takeLatest, call } from 'redux-saga/effects';
import environment from '../../../index';
import service, { APIcall } from '../../../services/service';
import { loaderAction } from '../../loader/loader.action';
import { TemplateListActions } from '../../TemplateList/action';
import { commonAction } from '../../commonActivity/action';

export const ViewTemplate = {
  DeleteTask: (val, taskid) => {
    console.log(val, taskid, 'valueinaction');
    return { type: 'DELETE_TASK_OF_TEMPLATE', payload: val, task: taskid };
  },
  AddTask: val => {
    return { type: 'ADD_TASK_TO_THE_TEMPLATE', payload: val };
  },
};

const methodPut = 'PUT'

function* deletetask({ payload, task }) {
  try {
    //var deletetask = JSON.stringify(payload);
    console.log(payload, task, 'tasktodelete');
    console.log(
      environment.TurnManagement.templateAPI + '/' + payload.turntemplateid,
      'conosle'
    );
    const response = yield APIcall(
      methodPut,
      environment.TurnManagement.templateAPI +
        '/' +
        payload.turntemplateid,
        //  +
        // '?' +
        // localStorage.getItem('token'),
      payload
    );

    console.log(response, 'deleteresponse');
    // if (response.status >= 200 && response.status <= 299) {
    yield put(TemplateListActions.getTemplateData());
    commonAction.openAlert({
      title: 'TemplateTask',
      content: task + ' ' + 'is Deleted from' + ' ' + payload.turntemplateid,
    });
  } catch (e) {
    // }
    console.log(e);
  }
}
function* AddTask({ payload }) {
  try {
    console.log(payload, 'addtaskstring');
    console.log(
      environment.TurnManagement.templateAPI + '/' + payload.id,
      'addtask binding'
    );
    const response = yield APIcall(
      methodPut,
      environment.TurnManagement.templateAPI +
      '/' +
      payload.id,
      //  +
      // '?' +
      // localStorage.getItem('token'),
      payload
    );

    console.log(
      environment.TurnManagement.templateAPI +
        '/' +
        payload.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
      payload,
      'environment console'
    );

    console.log(response, 'taskfromview response');
    if (response.status === 200) {
      yield put(TemplateListActions.getTemplateData());
      commonAction.openAlert({
        title: 'TemplateTask',
        content: 'NewTask ' + 'is Added To' + ' ' + payload.id,
      });
    }
  } catch (e) {
    console.log(e, 'event');
  }
}

export function* actionWatcher() {
  yield takeLatest('DELETE_TASK_OF_TEMPLATE', deletetask);
  yield takeLatest('ADD_TASK_TO_THE_TEMPLATE', AddTask);
}
