import { put, takeLatest, call, delay } from 'redux-saga/effects';
import service, { APIcall } from '../../../services/service';
import environment from '../../../index';
import templateConstant from './template.constant';
import { loaderAction } from '../../loader/loader.action';
import moment from 'moment';
import { array } from 'prop-types';
import { getToken } from '../../../auth/auth';
const { setLoader, removeLoader } = loaderAction;
export const templateActions = {
  getFLT_Template: () => {
    return { type: templateConstant.ASYNC_FLT_MGT_TEMPLATE_REQUEST };
  },
  updateDetails: data => {
    console.log(data, 'flightupdatedata');
    return {
      type: templateConstant.FLT_MGT_TEMPLATE_UPDATEDETAILS,
      payload: data,
    };
  },
};

// const methodPost = 'POST'
const methodGET = 'GET'
// const methodDelete = 'DELETE'
// // const methodPut = 'PUT'

function* getTemplate() {
  function success(payload) {
    console.log(payload, 'gettemplate');
    return { type: templateConstant.FLT_MGT_TEMPLATE_SUCCESS, payload };
  }
  function failure(error) {
    return { type: templateConstant.FLT_MGT_TEMPLATE_FAILURE, error };
  }

  yield put(setLoader());
  // const url = environment.isDevelopment
  // ? environment.FlightManagement.templateAPI
  // : environment.FlightManagement.templateAPI +
  //   '&fromdate=' +
  //   moment()
  //     .subtract(1, 'day')
  //     .toISOString().split('.')[0]+'Z' +
  //   '&todate=' +
  //   moment()
  //     .add(1, 'day')
  //     .toISOString().split('.')[0]+'Z' +
  //   '&' +
  //   getToken()
  try {
    // const response = yield call(service.get, url);
    const response = yield APIcall(
      methodGET,
      environment.FlightManagement.templateAPI +
      '&fromdate=' +
      moment()
        .subtract(1, 'day')
        .toISOString()
        .split('.')[0] +
      'Z' +
      '&todate=' +
      moment()
        .add(1, 'day')
        .toISOString()
        .split('.')[0] +
      'Z'
      // +
      // '&' +
      // getToken()
    );
    console.log(response, 'rurl');
    if (response.status === 200) {
      const updateDetails = generateUpdateDetails(response.data);
      yield put(success({ data: response.data, updateDetails }));
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

function generateUpdateDetails(value = []) {
  console.log(value, 'templateflight');
  return value.map(data => {
    let turninstanceId = data.turninstanceId;
    if (!Array.isArray(turninstanceId)) {
      turninstanceId = null;
    } else {
      if (turninstanceId.length) {
        turninstanceId = turninstanceId[0];
      } else {
        turninstanceId = null;
      }
    }
    return {
      id: data.id,
      turnId: turninstanceId,
      FlightSchedule: {
        ETD: data.estdeplcldttm,
        ATD: data.actdeplcldttm,
        OFF: data.offlcldttm,
        ETA: data.estarrlcldttm,
        ON: data.onlcldttm,
        ATA: data.actarrlcldttm,
      },
      FlightInformation: {
        AC_REG: data.aircraftregistrationno,
        Gate: data.depgateno,
        Pax_load: data.paxcount,
        PRM: data.ssrcount,
        Baggage: data.baggagecount,
        Bay: data.baynumber,
      },
      FlightTurnAround: { DoorClose: ' ' },
    };
  });
}

export function* fltMgtTemplateSaga() {
  yield takeLatest(
    templateConstant.ASYNC_FLT_MGT_TEMPLATE_REQUEST,
    getTemplate
  );
}
