import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from '../../index';
import { loaderAction } from '../loader/loader.action';

const { setLoader, removeLoader } = loaderAction;

export const DelayCodesActions = {
  getDelayCodesDetails: () => {
    return { type: 'GET_DelayCodes_Details_Async' };
  },
  postDelayCodesDetails: (obj, isEdit) => {
    return { type: 'POST_DelayCodes_Details', payload: { obj, isEdit } };
  },
  deleteDealyCode: id => {
    return { type: 'DELETE_DELAY_CODES_DETAILS', payload: id };
  },
};

const methodPost = 'POST'
const methodGET = 'GET'
const methodDelete = 'DELETE'
const methodPut = 'PUT'

function* DelayCodesInitdata() {
  try {
    yield put(setLoader());
    const url =
      environment.Datamaintenance.DelayCodesList;
    //  +
    // '?' +
    // localStorage.getItem('token');
    console.log(url, 'AcRegurl');

    const val = yield APIcall(methodGET, url);
    console.log(val.data, 'tessssssssst');

    yield put({
      type: 'Aircraft_DelayCodes_JsonResponse',
      payload: val.data,
    });
    yield put(removeLoader());
  } catch (e) {
    yield put(removeLoader());
  }
}

function* AddDelayCodes({ payload }) {
  try {
    let value;
    if (payload.isEdit) {
      var options = {
        url:
          environment.Datamaintenance.DelayCodesList +
          '/' +
          payload.obj.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPut,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      // console.log(value);
      console.log(payload.obj, 'tesssssting');

      const value = yield APIcall(
        methodPut,
        environment.Datamaintenance.DelayCodesList +
        '/' +
        payload.obj.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        payload.obj
      );
    } else {
      var options = {
        url:
          environment.Datamaintenance.DelayCodesList +
          '?' +
          localStorage.getItem('token'),
        method: methodPost,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      console.log(payload.obj, 'tessssstingPost');
      const value = yield APIcall(
        methodPost,
        environment.Datamaintenance.DelayCodesList,
        //  +
        //   '?' +
        //   localStorage.getItem('token'),
        payload.obj
      );
    }
    yield put(DelayCodesActions.getDelayCodesDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}
function* DeleteDelayCodes({ payload }) {
  try {
    var options = {
      url:
        environment.Datamaintenance.DelayCodesList +
        '/' +
        payload,
      //  +
      // '?' +
      // localStorage.getItem('token'),
      method: methodDelete,
      headers: {
        'Content-Type': 'application/json',
      },
      body: payload,
    };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );
    const value = yield APIcall(
      methodDelete,
      environment.Datamaintenance.DelayCodesList +
      '/' +
      payload
      // +
      // '?' +
      // localStorage.getItem('token')
    );

    // console.log(value);
    yield put(DelayCodesActions.getDelayCodesDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}
export function* actionWatcher() {
  yield takeLatest('GET_DelayCodes_Details_Async', DelayCodesInitdata);
  yield takeLatest('POST_DelayCodes_Details', AddDelayCodes);
  yield takeLatest('DELETE_DELAY_CODES_DETAILS', DeleteDelayCodes);
}
