import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';

const { setLoader, removeLoader } = loaderAction;

export const AircraftTypeActions = {
  getAircraftTypeDetails: () => {
    return { type: 'GET_AircraftType_Details_Async' };
  },
  postAircraftTypeDetails: (obj, isEdit) => {
    return { type: 'POSTED_AircraftType_Detail', payload: { obj, isEdit } };
  },
  deleteAircraftTypeDetails: id => {
    return { type: 'DELETE_AircraftType_Details', payload: id };
  },
};

const methodPost = 'POST'
const methodGET = 'GET'
const methodDelete = 'DELETE'
const methodPut = 'PUT'

function* AicraftTypedata() {
  try {
    yield put(setLoader());
    const url =
      environment.Datamaintenance.AcTypeList;
    //  +
    // '?' +
    // localStorage.getItem('token');
    // console.log(url, 'AcTypeurl');

    const val = yield APIcall(methodGET, url);
    console.log(val, 'AcTypeurl');
    yield put({ type: 'Aircraft_type_JsonResponse', payload: val.data });
    yield put(removeLoader());
  } catch (e) {
    yield put(removeLoader());
  }
}

function* AddAircraftType({ payload }) {
  console.log(payload, 'dd');
  try {
    if (payload.isEdit) {
      var options = {
        url:
          environment.Datamaintenance.AcTypeList +
          '/' +
          payload.obj.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPut,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      // console.log(value);

      const value = yield APIcall(
        methodPut,
        environment.Datamaintenance.AcTypeList +
        '/' +
        payload.obj.iataaircrafttype,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        payload.obj
      );
    } else {
      var options = {
        url:
          environment.Datamaintenance.AcTypeList,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPost,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      const value = yield APIcall(
        methodPost,
        environment.Datamaintenance.AcTypeList,
        //  +
        //   '?' +
        //   localStorage.getItem('token'),
        payload.obj
      );
    }
    yield put(AircraftTypeActions.getAircraftTypeDetails());
  } catch (e) {
    console.log(e);
  }
}

function* DeleteAircraft({ payload }) {
  try {
    var options = {
      url:
        environment.Datamaintenance.AcTypeList +
        '/' +
        payload,
      //  +
      // '?' +
      // localStorage.getItem('token'),
      method: methodDelete,
      headers: {
        'Content-Type': 'application/json',
      },
      body: payload,
    };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );

    // console.log(value);
    // yield put(AircraftTypeActions.getAircraftTypeDetails());
    const value = yield APIcall(
      methodDelete,
      environment.Datamaintenance.AcTypeList +
      '/' +
      payload
      // +
      // '?' +
      // localStorage.getItem('token')
    );
    yield put(AircraftTypeActions.getAircraftTypeDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_AircraftType_Details_Async', AicraftTypedata);
  yield takeLatest('POSTED_AircraftType_Detail', AddAircraftType);
  yield takeLatest('DELETE_AircraftType_Details', DeleteAircraft);
}
