const initialStationState = [
  {
    id: '',
    station: '',
    name: '',
    icaocode: '',
    region: '',
    city: '',
    country: '',
    gmtoffset: '',
    gmtvariance: '',
    isbasestation: 'N',
    mgt: '',
  },
];

export const Stationinitialdata = (state = initialStationState, action) => {
  let newState = [...state];
  switch (action.type) {
    case 'Station_JsonResponse':
      newState = [...action.payload];

      return newState;

    default: {
      return state;
    }
  }
};
