import { put, takeLatest, call, delay } from 'redux-saga/effects';

import service, { APIcall } from '../../../../services/service';
import fltTurnAroundConstant from './flightTurnAround.constant';
import { loaderAction } from '../../../loader/loader.action';
import store from '../../../store';
import { commonAction } from '../../../commonActivity/action';
import environment from '../../../..';

const { setLoader, removeLoader } = loaderAction;

export const flightTurnAroundAction = {
  getFLT_TurnAround: turnId => {
    return {
      type: fltTurnAroundConstant.ASYNC_FLT_MGT_FLIGHTTURNAROUND_REQUEST,
      turnId,
    };
  },
};

// const methodPost = 'POST'
const methodGET = 'GET'
// const methodDelete = 'DELETE'
// // const methodPut = 'PUT'


function* getFLT_TurnAround({ turnId }) {
  function success(payload) {
    console.log('turnnn', payload);
    return {
      type: fltTurnAroundConstant.FLT_MGT_FLIGHTTURNAROUND_SUCCESS,
      payload,
    };
  }

  function failure(error) {
    return {
      type: fltTurnAroundConstant.FLT_MGT_FLIGHTTURNAROUND_FAILURE,
      error,
    };
  }

  yield put(setLoader());
  const url = environment.isDevelopment
    ? environment.FlightManagement.flightTurnget
    : environment.FlightManagement.flightTurnget +
    turnId;
  //  +
  // '?' +
  // localStorage.getItem('token');
  try {
    // const response = yield call(
    //   service.get,
    const response = yield APIcall(
      methodGET,
      environment.FlightManagement.flightTurnget +
      turnId
      //  +
      // '?' +
      // localStorage.getItem('token')
    );

    // if (response.status === 500) {
    //   commonAction.openAlert({
    //     title: 'Warning',
    //     content: 'Turn instance id is null',
    //   });
    // yield put(success(response.data));
    // yield put(removeLoader());
    // }
    if (response.status === 200) {
      yield put(success(response.data));
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

export function* fltMgtTurnAroundSaga() {
  yield takeLatest(
    fltTurnAroundConstant.ASYNC_FLT_MGT_FLIGHTTURNAROUND_REQUEST,
    getFLT_TurnAround
  );
}
