import templateConstant from './template.constant';

const initialState = {
  isError: false,
  templateData: [],
  updateDetails: [],
};

export function fltMntTemplateReducer(state = initialState, action) {
  const newState = { ...state };

  if (action.type === templateConstant.FLT_MGT_TEMPLATE_SUCCESS) {
    newState.templateData = [...action.payload.data];
    newState.updateDetails = [...action.payload.updateDetails];
    return newState;
  }

  if (action.type === templateConstant.FLT_MGT_TEMPLATE_UPDATEDETAILS) {
    newState.updateDetails = [...action.payload];
    return newState;
  }

  if (action.type === templateConstant.FLT_MGT_TEMPLATE_FAILURE) {
    newState.isError = true;
    return newState;
  }
  return state;
}
