import loaderConstant from './loader.constant';

const initialState = {
  isLoading: false,
};

function loaderReducer(state = initialState, action) {
  const newState = { ...state };
  console.log(action.type);
  if (action.type === loaderConstant.SETLOADING) {
    newState.isLoading = true;
    return newState;
  }
  if (action.type === loaderConstant.REMOVELOADING) {
    newState.isLoading = false;
    return newState;
  }
  return state;
}

export default loaderReducer;
