const initialState = [
  {
    id: '',
    aircraftregno: '',
    aircrafttype: '',
    owner: '',
    cargopayload: '',
    overallpayload: '',
    ets: '',
    acarstype: '',
    fclasscapacity: '',
    bclasscapacity: '',
    pclasscapacity: '',
    eclasscapacity: '',
  },
];

export const Aircraftinitialdata = (state = initialState, action) => {
  let newState = [...state];
  switch (action.type) {
    case 'Aircraft_Registration_JsonResponse':
      newState = [...action.payload];
      console.log(newState, 'test');

      return newState;

    default: {
      return state;
    }
  }
};
