import { put, takeLatest, call, delay } from 'redux-saga/effects';

import service, { APIcall } from '../../../services/service';
import environment from '../../../index';
import flightUpdateConstant from './flightUpdate.constant';
import { loaderAction } from '../../loader/loader.action';
import { templateActions } from '../../flightManagement/template/template.action';
import store from '../../store';
import { commonAction } from '../../commonActivity/action';
import moment from 'moment';
import { getToken } from '../../../auth/auth';

const { setLoader, removeLoader } = loaderAction;
export const flightUpdateActions = {
  getFLT_Update: () => {
    return { type: flightUpdateConstant.ASYNC_FLT_UPDATE_REQUEST };
  },
  postFLT_Update: (obj, id) => {
    store.dispatch({ type: 'FlightDataSagaNew', payload: { obj, id } });
  },
  postFLT_ADDFlight: payload => {
    return { type: flightUpdateConstant.FLT_UPDATE_POST_REQUEST, payload };
  },
  storeFLT_Update: obj => {
    return {
      type: flightUpdateConstant.FLT_UPDATE_STORE_REQUEST,
      payload: obj,
    };
  },
  storePostFLT_Update: obj => {
    return {
      type: flightUpdateConstant.FLT_UPDATE_STORE_POST_REQUEST,
      payload: obj,
    };
  },
};



const methodPost = 'POST'
const methodGET = 'GET'
// const methodDelete = 'DELETE'
const methodPut = 'PUT'


function* getUpdate() {
  function successFltMgt(payload) {
    console.log('jsgd', payload);
    return { type: flightUpdateConstant.FLT_UPDATE_SUCCESS, payload };
  }
  function successTurn(payload) {
    return { type: flightUpdateConstant.FLT_TURN_SUCCESS, payload };
  }
  function failure(error) {
    return { type: flightUpdateConstant.FLT_UPDATE_FAILURE, error };
  }

  try {
    // const responseFLTMNG = yield call(
    //   service.get,
    const responseFLTMNG = yield APIcall(
      methodGET,
      environment.FlightManagement.getUpdate +
      '&fromdate=' +
      moment()
        .subtract(1, 'day')
        .toISOString()
        .split('.')[0] +
      'Z' +
      '&todate=' +
      moment()
        .add(1, 'day')
        .toISOString()
        .split('.')[0] +
      'Z'
      //  +
      // '&' +
      // localStorage.getItem('token')
    );

    if (responseFLTMNG.statusText == 'OK') {
      console.log(responseFLTMNG.data, 'RESOPNSEDATA');
      yield put(successFltMgt(responseFLTMNG.data));

      yield put(removeLoader());
    } else {
      throw new Error(responseFLTMNG);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

