export function* fltUpdateSaga() {
  yield takeLatest(flightUpdateConstant.ASYNC_FLT_UPDATE_REQUEST, getUpdate);
  yield takeLatest('FlightDataSagaNew', postFLT_Update);
  yield takeLatest(
    flightUpdateConstant.FLT_UPDATE_POST_REQUEST,
    postFLT_ADDFlight
  );
}
