import { put, takeLatest, call, delay } from 'redux-saga/effects';
import service, { APIcall } from '../../../services/service';
import environment from '../../../index';
import flightcrewConstant from './crewOnFlight.constant';
import { loaderAction } from '../../loader/loader.action';
const { setLoader, removeLoader } = loaderAction;
export const crewOnFlightActions = {
  getFLT_CREWONFLIGHT: id => {
    return { type: flightcrewConstant.ASYNC_FLT_MGT_FLIGHTCREW_REQUEST, id };
  },
};

const methodGET = 'GET'


function* getCrewOnFlight({ id }) {
  console.log(id, 'id crewonflight');
  function success(payload) {
    return { type: flightcrewConstant.FLT_MGT_FLIGHTCREW_SUCCESS, payload };
  }
  function failure(error) {
    return { type: flightcrewConstant.FLT_MGT_FLIGHTCREW_FAILURE, error };
  }

  yield put(setLoader());
  const url = environment.isDevelopment
    ? environment.FlightManagement.flt_crewAPI
    : environment.FlightManagement.flt_crewAPI +
    id +
    '/crew';
  //  +
  // '?' +
  // localStorage.getItem('token');
  try {
    // const response = yield call(
    //   service.get,
    const response = yield APIcall(
      methodGET,
      environment.FlightManagement.flt_crewAPI +
      id +
      '/crew'
      //  +
      // '?' +
      // localStorage.getItem('token')
    );
    console.log(response.data.crews, 'crew');
    if (response.status === 200) {
      if (Array.isArray(response.data.crews)) {
        yield put(success(response.data.crews));
      } else {
        yield put(success([]));
      }

      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

export function* fltcrewdetailsSaga() {
  yield takeLatest(
    flightcrewConstant.ASYNC_FLT_MGT_FLIGHTCREW_REQUEST,
    getCrewOnFlight
  );
}
