import fileuploadConstant from './file-upload.constant';

const initialState = {
  uploadDetails: [],
  errorDetails: [],
  isError: false,
};

export default function fileuploadReducer(state = initialState, action) {
  const newState = { ...state };

  if (action.type === fileuploadConstant.FLT_MGT_FILE_UPLOAD_INFO_SUCCESS) {
    newState.uploadDetails = [...action.payload];
    console.log(...action.payload, 'uploaddetails');
    console.log('details', newState.uploadDetails);
    return newState;
  }
  if (action.type === fileuploadConstant.FLT_MGT_FILE_UPLOAD_INFO_FAILURE) {
    newState.isError = true;
    return newState;
  }

  if (action.type === fileuploadConstant.FLT_MGT_FILE_UPLOAD_SUCCESS) {
    newState.errorDetails = [...action.payload];
    console.log(...action.payload, 'kiiik');
    console.log('hiiik', newState.errorDetails);
    return newState;
  }
  if (action.type === fileuploadConstant.FLT_MGT_FILE_UPLOAD_FAILURE) {
    newState.isError = true;
    return newState;
  }
  return state;
}
