// import { confirmAlert } from 'react-confirm-alert';
import React from 'react';
import './alert.scss';
import { connect } from 'react-redux';
import { alertAction } from '../../../Store/commonActivity/alert/alert.action';
import Button from '../../../components/Atoms/Button';
import Animate from '../../../components/Atoms/addAnimation';

class Alert extends React.Component {
  render() {
    console.log(this.props);
    return (
      <Animate
        show={this.props.isOpen}
        className="Alert-bg-container"
        enterAnimate="zoom-out-enter .4s"
        leaveAnimate="zoom-out-leave .3s"
      >
        <div className="Alert-container">
          <div className="Alert-title">
            <b>{this.props.content.title}</b>
          </div>
          <div className="Alert-message">{this.props.content.content}</div>
          <div className="Alert-control">
            <div className="buttonflex">
              <Button
                className="primary"
                ButtonHandler={() => {
                  // console.log(this);

                  this.props.closeAlert();
                }}
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      </Animate>
    );
  }
}
function mapState(state) {
  return { ...state.commonActivityReducer.alertReducer };
}
const actionCreators = {
  closeAlert: alertAction.closeAlert,
};

export default connect(mapState, actionCreators)(Alert);
