import React from 'react';
import './Button.scss';

class Button extends React.Component {
  clicked = e => {
    if (
      this.props.ButtonHandler &&
      typeof this.props.ButtonHandler === 'function'
    ) {
      this.props.ButtonHandler(e);
    }
  };
  render() {
    return (
      <>
        <button
          className={'button ' + this.props.className}
          onClick={this.clicked}
        >
          {this.props.children}
        </button>
      </>
    );
  }
}

Button.defaultProps = {
  className: '',
  ButtonHandler: () => {},
  text: '',
};

export default Button;
