import { all } from 'redux-saga/effects';
import { authenticationSaga } from './login/user.action';
import { actionWatcher as ManageDepartment } from './ManageDepartment/action';
import { actionWatcher as ManageTurnType } from './ManageTurnType/action';
import { actionWatcher as ManageTask } from './ManageTask/action';
import { actionWatcher as TemplateList } from './TemplateList/action';
import { CreateTemplateSaga } from './TurnManagement/CreateTurnTemplate/action';
import { fltMgtTemplateSaga } from './flightManagement/template/template.action';
import { fltUpdateSaga } from './flightManagement/flightUpdate/flightUpdate.action';
import { actionWatcher as Tep_View_Del_Task } from './../Store/TurnManagement/Tep_View_Del_Task/action';
import { fltcrewdetailsSaga } from './flightManagement/crewOnFlight/crewOnFlight.action';
import { actionWatcher as Stationdata } from './Station/action';
import { actionWatcher as AicraftRegdata } from '../Store/AircraftReg/action';
import { actionWatcher as AircraftType } from '../Store/AircraftType/action';
import { actionWatcher as DelayCodesInitdata } from '../Store/DelayCodes/action';
import { fltMgtTurnAroundSaga } from './flightManagement/flightUpdate/flightTurnAround/flightTurnAround.action';
import { fltFileUploadErrordetailsSaga } from './flightManagement/fileUpload/file-upload.action';

export default function* rootSaga() {
  console.log('Inside RootSaga Inside Saga');

  yield all([
    // Stations(),
    AircraftType(),
    authenticationSaga(),
    ManageDepartment(),
    ManageTurnType(),
    ManageTask(),
    TemplateList(),
    Tep_View_Del_Task(),
    CreateTemplateSaga(),
    fltMgtTemplateSaga(),
    fltUpdateSaga(),
    fltcrewdetailsSaga(),
    Stationdata(),
    AicraftRegdata(),
    DelayCodesInitdata(),
    fltMgtTurnAroundSaga(),
    fltFileUploadErrordetailsSaga(),
  ]);
}
