import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import { loaderAction } from './../loader/loader.action';
import environment from './../../index';

const { setLoader, removeLoader } = loaderAction;

export const TemplateListActions = {
  getTemplateData: () => {
    return { type: 'GET_TEMPLATELIST_ASYNC' };
  },
};
// const methodPost = 'POST'
 const methodGET = 'GET'
// const methodDelete = 'DELETE'
// // const methodPut = 'PUT'
function* ManageTemplateData() {
  try {
    yield put(setLoader());
    const val = yield APIcall(methodGET, environment.TurnManagement.templateAPI);
    console.log(val, 'template val');
    if (val.status >= 200 && val.status <= 299) {
      yield put({ type: 'TEMPLATELIST_JSONRESPONSE', json: val });
    }
    yield put(removeLoader());
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_TEMPLATELIST_ASYNC', ManageTemplateData);
}
