import { put, takeLatest, call, delay } from 'redux-saga/effects';
import { APIcall } from '../../../services/service';
import environment from '../../../index';
import fileuploadConstant from './file-upload.constant';
import { loaderAction } from '../../loader/loader.action';
const { setLoader, removeLoader } = loaderAction;
export const fileUploadAction = {
  postFLT_UPLOAD_INFO: () => {
    return { type: fileuploadConstant.ASYNC_FLT_MGT_FILE_UPLOAD_INFO_REQUEST };
  },
  getFLT__UPLOAD_INFO: payload => {
    return {
      type: fileuploadConstant.POST_FLT_MGT_FILE_UPLOAD_REQUEST,
      payload,
    };
  },
  getFLT__ERROR_MESSAGE: (filename, dateOfUpload) => {
    return {
      type: fileuploadConstant.ASYNC_FLT_MGT_FILE_UPLOAD_REQUEST,
      errorforfile: filename,
      date: dateOfUpload,
    };
  },
};

function* postFLT_UPLOAD_INFO({ payload }) {
  try {
    // var val = JSON.stringify(payload);
    console.log(payload, 'createtemplatestringify');
    const value = yield APIcall(
      'POST',
      environment.FlightManagement.fileUpload,
      //  +
      //   '?' +
      //   localStorage.getItem('token'),
      payload,
      {
        'Content-Type': 'multipart/form-data',
      }
    );
    // if (value.status >= 200 && value.status <= 299) {
    console.log(value, 'postEDDD');
  } catch (e) {
    console.log(e);
  }
}

function* getFLT__UPLOAD_INFO() {
  console.log('Before Servuce');
  function success1(payload) {
    console.log(payload, 'response upload');
    return {
      type: fileuploadConstant.FLT_MGT_FILE_UPLOAD_INFO_SUCCESS,
      payload,
    };
  }

  function failure1(error) {
    return { type: fileuploadConstant.FLT_MGT_FILE_UPLOAD_INFO_FAILURE, error };
  }
  try {
    yield put(setLoader());
    console.log(
      'before service',
      environment.FlightManagement.flt_fileUploadInfo
    );
    const response = yield APIcall(
      'GET',
      environment.FlightManagement.flt_fileUploadInfo
      // +
      //   '?' +
      //   localStorage.getItem('token')
    );
    console.log(response, 'uploaddata');
    // const response = yield call(
    //   service.get,
    // const response = yield APIcall('GET', url);
    console.log(response.status, 'upload data');
    if (response.status === 200) {
      if (Array.isArray(response.data)) {
        yield put(success1(response.data));
      } else {
        yield put(success1([]));
      }

      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e, 'newError');
    yield put(removeLoader());
    yield put(failure1(e));
  }
}
function* getFLT__ERROR_MESSAGE({ errorforfile, date }) {
  console.log(errorforfile, date, 'error and dateofupload');
  function success() {
    console.log('hgsfd');
    return { type: fileuploadConstant.FLT_MGT_FILE_UPLOAD_SUCCESS };
  }

  function failure(error) {
    return { type: fileuploadConstant.FLT_MGT_FILE_UPLOAD_FAILURE, error };
  }
  try {
    yield put(setLoader());
    const url =
      environment.FlightManagement.flt_fileUploadErrorMsg +
      errorforfile +
      '/' +
      'dateofupload' +
      '/' +
      '%22' +
      date +
      '%22';
    // const response = yield call(
    //   service.get,
    const response = yield APIcall(
      'GET',
      url
      // + '?' + localStorage.getItem('token')
    );
    console.log(url, 'urlfor delete');
    console.log(response, 'fileupload response');
    if (response.status === 200) {
      if (Array.isArray(response.data)) {
        yield put(success(response.data));
      } else {
        yield put(success([]));
      }
      yield put(removeLoader());
    } else {
      throw new Error(response);
    }
    //
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
    yield put(failure(e));
  }
}

export function* fltFileUploadErrordetailsSaga() {
  yield takeLatest(
    fileuploadConstant.POST_FLT_MGT_FILE_UPLOAD_REQUEST,
    getFLT__UPLOAD_INFO
  );
  yield takeLatest(
    fileuploadConstant.ASYNC_FLT_MGT_FILE_UPLOAD_INFO_REQUEST,
    postFLT_UPLOAD_INFO
  );
  yield takeLatest(
    fileuploadConstant.ASYNC_FLT_MGT_FILE_UPLOAD_REQUEST,
    getFLT__ERROR_MESSAGE
  );
}
