import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';

const { setLoader, removeLoader } = loaderAction;

export const AircraftRegActions = {
  getAircraftRegDetails: () => {
    console.log('fromgetaircraft');
    return { type: 'GET_AircraftReg_Details_Async' };
  },
  postAircraftRegDetails: (obj, isEdit) => {
    return { type: 'POST_AircraftReg_Details', payload: { obj, isEdit } };
  },
  deleteAircraftRegDetails: id => {
    return { type: 'DELETE_AircraftReg_Details', payload: id };
  },
};


const methodPost = 'POST'
const methodGET = 'GET'
// const methodDelete = 'DELETE'
const methodPut = 'PUT'

function* AicraftRegdata() {
  try {
    yield put(setLoader());
    const url =
      environment.Datamaintenance.AcRegList;
    //  +
    // '?' +
    // localStorage.getItem('token');

    const val = yield APIcall(methodGET, url);
    yield put({
      type: 'Aircraft_Registration_JsonResponse',
      payload: val.data,
    });
    yield put(removeLoader());
  } catch (e) {
    yield put(removeLoader());
  }
}

function* AddAircraftReg({ payload }) {
  try {
    if (payload.isEdit) {
      var options = {
        url:
          environment.Datamaintenance.AcRegList +
          '/' +
          payload.obj.id,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPut,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      // console.log(value);
      const value = yield APIcall(
        methodPut,
        environment.Datamaintenance.AcRegList +
        '/' +
        payload.obj.aircraftregno,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        payload.obj
      );
    } else {
      var options = {
        url:
          environment.Datamaintenance.AcRegList,
        //  +
        // '?' +
        // localStorage.getItem('token'),
        method: methodPost,
        headers: {
          'Content-Type': 'application/json',
        },
        body: payload.obj,
      };
      // const value = yield call(
      //   service.post,
      //   'http://10.11.131.65:7584/api',
      //   options
      // );
      const value = yield APIcall(
        methodPost,
        environment.Datamaintenance.AcRegList,
        //  +
        //   '?' +
        //   localStorage.getItem('token'),
        payload.obj
      );
    }

    yield put(AircraftRegActions.getAircraftRegDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}

function* DeleteAircraftReg({ payload }) {
  try {
    var options = {
      url:
        environment.Datamaintenance.AcRegList +
        '/' +
        payload,
      //  +
      // '?' +
      // localStorage.getItem('token'),
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
      body: payload,
    };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );
    const value = yield APIcall(
      'DELETE',
      environment.Datamaintenance.AcRegList +
      '/' +
      payload
      // +
      // '?' +
      // localStorage.getItem('token')
    );

    console.log(value);
    yield put(AircraftRegActions.getAircraftRegDetails());
  } catch (e) {
    console.log(e, 'Error');
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_AircraftReg_Details_Async', AicraftRegdata);
  yield takeLatest('POST_AircraftReg_Details', AddAircraftReg);
  yield takeLatest('DELETE_AircraftReg_Details', DeleteAircraftReg);
}
