import flightcrewConstant from './crewOnFlight.constant';

const initialState = {
  isError: false,
  crew: [],
};

export default function fltMntCrewOnFlightReducer(
  state = initialState,
  action
) {
  const newState = { ...state };

  if (action.type === flightcrewConstant.FLT_MGT_FLIGHTCREW_SUCCESS) {
    newState.crew = [...action.payload];
    
    return newState;
  }
  if (action.type === flightcrewConstant.FLT_MGT_FLIGHTCREW_FAILURE) {
    newState.isError = true;
    return newState;
  }
  return state;
}
