const initailstate = {
  data: [],
};

export const TurnTemplateInitialData = (state = initailstate, action) => {
  let newState = { ...state };
  switch (action.type) {
    case 'TEMPLATELIST_JSONRESPONSE':
      newState.data = [...action.json.data];
      return newState;
    default:
      console.log(action, state, 'TEMPLATELIST_JSONRESPONSE');

      return state;
  }
};
