import { put, takeLatest, call } from 'redux-saga/effects';
import service, { APIcall } from '../../services/service';
import environment from './../../index';
import { loaderAction } from './../loader/loader.action';
import { commonAction } from '../commonActivity/action';

const { setLoader, removeLoader } = loaderAction;

export const ManageTurnTypeActions = {
  getTurnTypeDetails: () => {
    return { type: 'GET_TURNTYPE_DETAILS_ASYNC' };
  },
  postTurnTypeDetails: obj => {
    return { type: 'POST_TURNTYPE_DETAILS', payload: obj };
  },
  deleteTurnTypeDetails: val => {
    console.log(val, 'from actionturntype');
    return { type: 'DELETE_TURNTYPE_DETAILS', payload: val };
  },
};

const methodPost = 'POST'
const methodGET = 'GET'
const methodDelete = 'DELETE'
// const methodPut = 'PUT'

function* Manageturntypedata() {
  try {
    yield put(setLoader());
    const val = yield APIcall(methodGET, environment.TurnManagement.manageturntype);
    console.log(val, 'from turntype getturntype');
    if (val.status >= 200 && val.status <= 299) {
      yield put({ type: 'MANAGE_TURNTYPE_JSON_RESPONSE', json: val });
      yield put(removeLoader());
    }
  } catch (e) {
    console.log(e);
    yield put(removeLoader());
  }
}
function* DeleteTurntype({ payload }) {
  try {
    yield put(setLoader());
    var val = JSON.stringify(payload);
    //var val = JSON.stringify(payload);
    console.log(val, 'payload');
    const value = yield APIcall(
      methodDelete,
      environment.TurnManagement.manageturntype + '/' + payload.id
    );
    //  const value = yield APIcall('DELETE', url);
    console.log(
      environment.TurnManagement.manageturntype + '/' + payload.id,
      'deleteturntype'
    );
    yield put(ManageTurnTypeActions.getTurnTypeDetails());
    yield put(removeLoader());
    commonAction.openAlert({
      title: 'TurnType',
      content: payload.id + ' ' + 'is Deleted from List ',
    });
  } catch (e) {
    console.log(e);
  }
}

function* AddTurnType({ payload }) {
  try {
    // var options = {
    //   url:
    //     environment.TurnManagement.manageturntype +
    //     '?' +
    //     localStorage.getItem('token'),
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json',
    //   },
    //   body: payload,
    // };
    // const value = yield call(
    //   service.post,
    //   'http://10.11.131.65:7584/api',
    //   options
    // );
    var val = JSON.stringify(payload);
    console.log(val, 'type call');
    const value = yield APIcall(
      methodPost,
      environment.TurnManagement.manageturntype,
      val
    );
    // console.log(value,"from turntype addturntype")
    //if(val.status >=200 && val.status <= 299){
    yield put(ManageTurnTypeActions.getTurnTypeDetails());
    commonAction.openAlert({
      title: 'Turn Type',
      content: 'New TurnType' + ' ' + 'is Added To The' + ' ' + 'List',
    });
    // }
  } catch (e) {
    console.log(e);
  }
}

export function* actionWatcher() {
  yield takeLatest('GET_TURNTYPE_DETAILS_ASYNC', Manageturntypedata);
  yield takeLatest('POST_TURNTYPE_DETAILS', AddTurnType);
  yield takeLatest('DELETE_TURNTYPE_DETAILS', DeleteTurntype);
}
