import requireContext from 'require-context.macro';

const importAll = r => {
  let files = {};
  r.keys().map((item, index) => {
    return (files[item.split(/[/]+/).pop()] = r(item));
  });
  return files;
};

export const Allimages = importAll(
  requireContext('../', true, /\.(png|jpe?g|svg)$/)
);
