const initialrAcTypeState = [
  {
    id: '',
    internalaircrafttype: '',
    iataaircrafttype: '',
    owner: '',
    fleetcategory: '',
    cargopayload: '',
    overallpayload: '',
    mgt: '',
    capacitydescription: '',
    crewbank: '',
    crewbank: '',
    fseattype: '',
    fcapacity: '',
    ftoilets: '',
    bseattype: '',
    bcapacity: '',
    btoilets: '',
    pseattype: '',
    pcapacity: '',
    ptoilets: '',
    eseattype: '',
    ecapacity: '',
    etoilets: '',
    remarks: '',
    gallerytype: '',
  },
];

export const AircrafttypeReducer = (state = initialrAcTypeState, action) => {
  let newState = { ...state };
  switch (action.type) {
    case 'Aircraft_type_JsonResponse':
      newState = [...action.payload];

      return newState;

    default: {
      return state;
    }
  }
};
